<template>
    <div id="merchantList">
        <el-header class="el-head-child">
            
        </el-header>
        <div class="search" style="background-color: #f0f3f6;padding-top: 20px;">
            <div class="inputs">
                <div>
                    <span>// : </span>
                    <el-input style="margin-right: 30px" class="merchat" v-model='searchValue.commodityName'></el-input>
                    <span> : </span>
                    <el-date-picker
                        v-model="searchValue.timeBegin"
                        type="date"
                        placeholder=""
                        value-format="yyyy-MM-dd">
                    </el-date-picker>
                        
                    <el-date-picker
                        v-model="searchValue.timeEnd"
                        type="date"
                        placeholder=""
                        value-format="yyyy-MM-dd">
                    </el-date-picker>
                </div>
                <div class="inputs-bottom">
                    <span> : </span>
                    <el-select placeholder="" v-model='searchValue.industryValue'>
                        <el-option v-for='(item, index) in search.industry' :key='"chantListind" + index' :label='item.value' :value='item.orderno'></el-option>
                    </el-select>
                    <span style="margin-left: 30px;">: </span>
                    <el-select placeholder="" v-model='searchValue.provinceValue' @change='changeProvinceToGetCity'>
                        <el-option v-for='(item, index) in search.province' :key='"chantListpro" + index' :label='item.areaName' :value='item.areaId'></el-option>
                    </el-select>
                    <el-select placeholder="" v-model='searchValue.cityValue'>
                        <el-option v-for='(k, index) in search.city' :key='"chantListCity" + index' :label='k.areaName' :value='k.areaId'></el-option>
                    </el-select>
                    <span style="margin-left: 30px;">: </span>
                    <el-select v-model='searchValue.agentValue' placeholder="">
                        <el-option v-for='(k, index) in search.agent' :key='"chantListAgent" + index' :label='k.agentName' :value='k.agentSeq'></el-option>
                    </el-select>
                    <el-button type="danger" @click='tableInit()'></el-button>
                    <!-- <el-button type="primary" ></el-button> -->
                </div>
            </div>
        </div>

        <el-main>
            <el-table ref="table" tooltip-effect="dark"
                :data="tableData"
                style="width: 100%"
                stripe
                @cell-click='tableClick'>
                    <el-table-column prop='mercSeq' label=""></el-table-column>
                    <el-table-column prop="mercName" label=""></el-table-column>
                    <el-table-column prop="industryName" label=""></el-table-column>
                    <el-table-column prop="area" label=""></el-table-column>
                    <el-table-column prop="portNum" label=""></el-table-column>
                    <el-table-column prop="mercBeginTime" label=""></el-table-column>
                    <el-table-column prop="agentName" label=""></el-table-column>
                    <el-table-column prop="mercBusinessEntityContactName" label=""></el-table-column>
                    <el-table-column prop="mercBusinessEntityContactPhone" label=""></el-table-column>
                    <el-table-column prop="mercStatus" label=""></el-table-column>
                    <el-table-column prop="name" label="">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click='datumShow = true'></el-button>
                        </template>
                    </el-table-column>
            </el-table>
            <el-pagination
                background
                layout="total, sizes, prev, pager, next, jumper"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="page.pagecurrent"
                :total='page.pagecount'
                v-if="page.pagecount != 0">
            </el-pagination>

            <!--  -->
            <el-dialog
            :visible.sync="datumShow"
            width="50%">
            <div class="top">
                
            </div>
            <el-row>
                <el-col :span="12">:{{ datumData.agentName }}</el-col>
                <el-col :span="12">:</el-col>
            </el-row>
            <el-row>
                <el-col :span="12">:{{ datumData.area }}</el-col>
                <el-col :span="12">:{{ datumData.industryName }}</el-col>
            </el-row>
            <el-row>
                <el-col :span="12">/:{{ datumData.mercBusinessLicense }}</el-col>
                <el-col :span="12">:{{ datumData.agentName }}</el-col>
            </el-row>
            <el-row>
                <el-col :span="12">:{{ datumData.mercBusinessEntityName }}</el-col>
                <el-col :span="12">:{{ datumData.mercBusinessId }}</el-col>
            </el-row>
            <el-row>
                <el-col :span="12">:{{ datumData.mercBusinessEntityContactName }}</el-col>
                <el-col :span="12">:{{ datumData.mercBusinessEntityContactPhone }}</el-col>
            </el-row>
            <el-row>
                <el-col :span="12">:{{ datumData.mercBusinessMail }}</el-col>
            </el-row>
            <div class="top">
                
            </div>
            <img src="/static/blue-2.png" alt="">
            <div class="top">
                
            </div>
            <el-row>
                <el-col :span="12">:{{ datumData.mercStatus }}
                    <el-button type='primary' v-show='frozen' @click='frozenClick'></el-button>
                    <el-button type='primary' v-show='thaw' @click='thawClick'></el-button>
                </el-col>
                <el-col :span="12">:</el-col>
            </el-row>
            <el-row>
                <el-col :span="12">:{{ datumData.mercBeginTime }}</el-col>
                <el-col :span="12">:</el-col>
            </el-row>
            <el-row>
                <el-col :span="12">:{{ datumData.frozenCause }}</el-col>
            </el-row>
            <span slot="footer" class="dialog-footer">
            <el-button @click="datumShow = false"></el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog
        title=""
        :visible.sync="frozenShow"
        width="30%">
        
        <el-input type='textarea' resize='none' v-model='frozenReason'></el-input>
        <span slot="footer" class="dialog-footer">
            <el-button @click="frozenShow = false"> </el-button>
            <el-button type="primary" @click="frozenSubmit"> </el-button>
        </span>
        </el-dialog>

        <!--  -->
        <el-dialog
        title=""
        :visible.sync="thawShow"
        width="30%">
        <span></span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="thawShow = false"> </el-button>
            <el-button type="primary" @click="thawSubmit"> </el-button>
        </span>
        </el-dialog>

        <!--  -->
        <el-dialog
        title=""
        :visible.sync="successShow"
        width="30%">
        <span slot="footer" class="dialog-footer">
            <el-button @click="successShow = false"> </el-button>
            <el-button type="primary" @click="successShow = false"> </el-button>
        </span>
        </el-dialog>
        </el-main>
    </div>
</template>
<script>
import {baseUrl} from '../assets/api/api'

    export default {
        data() {
            return {
                tableData: [], //
                search: {
                    industry: [],    //
                    province: [],   //
                    city: [],    //
                    agent: []
                },
                searchValue: {
                    cityValue: '',
                    provinceValue: '',
                    industryValue: '',
                    agentValue: '',
                    timeBegin: '',
                    timeEnd: '',
                    commodityName: ''   //
                },


                cIndustry: '', //
                cProvince: '',  //
                cCity: '',  //
                merchantFrom: '',      //

                pageNum: 1,
                pageSize: 10,

                datumShow: false,

                frozen: false,   //
                thaw: false,    //

                row: {},

                datumData: {},

                frozenShow: false,
                thawShow: false,
                successShow: false,

                frozenReason: '',    //
                page: {
                    pagecount: '',
                    pagecurrent: 1
                }
            }
        },
        created: function() {
            var _this = this;
            // 
            var timeNow = new Date().getTime();
            this.tableInit(1, 10);
            // 
            this.$axios.get(baseUrl + '/sysdicttreedata/getCategory').then(res => {
                _this.search.industry = res.data;
            }).catch(err => {
                console.log(err);
            })
            // 
            this.$axios.get(baseUrl + '/areacode/getProvince').then(res => {
                _this.search.province = res.data;
            }).catch(err => {
                console.log(err);
            })
            //
            this.$axios.get(baseUrl + '/agent/getAgentList').then(res => {
                _this.search.agent = res.data;
            }).catch(err => {
                console.log(err);
            })
        },
        mounted: function() {
        },
        methods: {
            handleSizeChange(val) {
                this.tableInit(1, val);
            },
            handleCurrentChange(val) {
                this.tableInit(val, 10);
            },
            /**
             * 
             * @param  {[type]} page [description]
             * @param  {[type]} size [description]
             * @return {[type]}      [description]
             */
            tableInit: function(page, size) {
                var _this = this;
                $.ajax({
                    url: baseUrl + '/merchant/searchMerchantList',
                    type: 'post',
                    data: {
                        mercName: _this.searchValue.commodityName,
                        startTime: _this.searchValue.timeBegin,
                        endTime: _this.searchValue.timeEnd,
                        mercIndustry: _this.searchValue.industryValue,
                        mercProvince: _this.searchValue.provinceValue,
                        mercCity: _this.searchValue.cityValue,
                        agentSeq: _this.searchValue.agentValue,
                        page: page,
                        size: size
                    },
                    success: function(res) {
                        console.log(res);
                        res.dataInfo.forEach(function(ele, index) {
                            //remainTime
                            ele.mercBeginTime = new Date(ele.mercBeginTime).getFullYear() + '-' + (new Date(ele.mercBeginTime).getMonth() + 1) + '-' + new Date(ele.mercBeginTime).getDate();
                            // 
                            ele.area = ele.provinceName + ele.cityName;
                            //
                            if (ele.mercStatus == 0) {
                                ele.mercStatus = '';
                            } else if (ele.mercStatus == 1) {
                                ele.mercStatus = '';
                            } else if (ele.mercStatus == 2) {
                                ele.mercStatus = '';
                            }
                        })
                        _this.tableData = res.dataInfo;
                        _this.page.pagecount = res.count;
                    },
                    error: function(err) {
                        console.log(err);
                    }
                })
            },
            /**
             * 
             * @return {[type]} [description]
             */
            searchSubmit: function() {
                var _this = this;

            },

            tableClick(row) {
                var _this = this;
                this.row = row;
                //
                $.ajax({
                    url: baseUrl + '/merchant/getMerchantByMercSeq',
                    type: 'post',
                    data: {
                        mercSeq: _this.row.mercSeq
                    },
                    success: function(res) {
                        _this.datumData = res;
                        _this.datumData.area = _this.datumData.provinceName + _this.datumData.cityName;
                        var now = new Date(_this.datumData.mercBeginTime);
                        _this.datumData.mercBeginTime = now.getFullYear() + '-' + (now.getMonth() + 1) + '-' + now.getDate();
                        if (_this.datumData.mercStatus == 0) {
                            _this.datumData.mercStatus = '';
                            _this.frozen = true;
                            _this.thaw = false;
                        } else if (_this.datumData.mercStatus == 2) {
                            _this.datumData.mercStatus = '';
                            _this.thaw = true;
                            _this.frozen = false;
                        }
                     },
                    error: function(err) {
                        console.log(err);
                    }
                })
            },

            frozenClick: function() {
                this.frozenShow = true;
                this.datumShow = false;
            },
            thawClick: function() {
                this.thawShow = true;
                this.datumShow = false;
            },
            // 
            frozenSubmit: function() {
                var _this = this;
                $.ajax({
                    url: baseUrl + '/merchant/updateMercStatus',
                    type: 'post',
                    data: {
                        mercSeq: _this.row.mercSeq,
                        mercStatus: 2,
                        frozenCause: _this.frozenReason
                    },
                    success: function(res) {
                        _this.frozenShow = false;
                        _this.successShow = true;
                        _this.tableInit(1, 10);
                        _this.page.pagecurrent = 1;
                    },
                    error: function(err) {
                        console.log(err);
                    }
                })
            },
            // 
            thawSubmit: function() {
                var _this = this;
                $.ajax({
                    url: baseUrl + '/merchant/updateMercStatus',
                    type: 'post',
                    data: {
                        mercSeq: _this.row.mercSeq,
                        mercStatus: 0
                    },
                    success: function(res) {
                        _this.thawShow = false;
                        _this.successShow = true;
                        _this.row.mercStatus = '';
                        _this.tableInit(1, 10);
                        _this.page.pagecurrent = 1;
                    },
                    error: function(err) {
                        console.log(err);
                    }
                })
            },
            changeProvinceToGetCity() {
                var _this = this;
                $.ajax({
                    url: baseUrl + '/areacode/getCity',
                    type: 'post',
                    data: {
                        areaSupCode: _this.searchValue.provinceValue
                    },
                    success: function(res) {
                        _this.search.city = res;
                    },
                    error: function(err) {
                        console.log(err);
                    }
                })
            },
            changeCity(name) {
                this.cCity = name;
            }
        }
    }
</script>
<style lang="less">
html, body, #app, .el-container {
    height: 100%;
}
#merchantList {
    .el-header.el-head-parent {
        background-color: #313540;
        color: #333;
        line-height: 60px;
        span {
            color: #fff;
            font-size: 16px;
            font-weight: bold;
            letter-spacing: 3px;
        }
    }
    .el-header.el-head-child {
        color: #3399ff;
        font-size: 18px;
        line-height: 60px;
        background-color: #fff;
    }
    .el-aside {
        background-color: #313540;
        border-top: 1px solid #464c5b;
    }
    .el-main {
        background-color: #f0f3f6;
        padding: 0;
    }
    .inputs {
        background-color: #fff;
        padding: 24px 20px;
    }
    .inputs input {
        display: inline-block;
        width: 140px;
        height: 40px;
        background-color: #f0f3f6;
        border: 1px solid #dfe3eb;
        border-radius: 3px;
    }
    .inputs span {
        letter-spacing: 3px;
        color: #333;
        font-size: 16px;
    }
    .inputs .merchat input {
        width: 180px;
    }
    .el-input {
        width: auto;
    }
    .inputs .inputs-bottom {
        margin-top: 30px;
    }
    .inputs .inputs-bottom input {
        width: 120px;
    }
    .inputs .el-button span {
        color: #fff;
    }
    .el-table {
        margin-top: 20px;
    }
    .el-table thead {
        color: #333;
        font-size: 16px;
    }
    .el-pagination {
        text-align: center;
        margin-top: 30px;
    }
    .cell {
        text-align: center;
    }
    .el-dialog {
        .top {
            color: #333;
            font-size: 18px;
            height: 40px;
            line-height: 40px;
            background-color: #f0f3f6;
            margin: 0 -20px;
            padding: 0 20px;
        }
    }
}
</style>



