<template lang="html">
<!-- sim -->
<el-container class="simCard">
    <el-header>
        sim
        <el-button type='primary' @click='saveInformation = true'>
            
        </el-button>
    </el-header>
    <el-main>
        <!--  -->
        <div class="search">
            <div class="center">
                sim:
                <el-input v-model="search.name"></el-input>
                :
                <el-input v-model="search.code"></el-input>
                :
                <el-input v-model="search.number"></el-input>
                <el-button type='danger' @click="tableInit(1, 10)"></el-button>
            </div>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data='tableData' tooltip-effect="dark" style="width: 100%" stripe @cell-click='uniteChange'>
            <el-table-column type="selection"></el-table-column>
            <el-table-column prop='simNum' label="sim"></el-table-column>
            <el-table-column prop="simMachineCode" label=""></el-table-column>
            <el-table-column prop="simHubIndex" label=""></el-table-column>
            <el-table-column prop="mercName" label=""></el-table-column>
            <el-table-column prop="entry_time" label=""></el-table-column>
            <el-table-column prop="agentAllow_time" label=""></el-table-column>
            <el-table-column prop="mercAllow_time" label=""></el-table-column>
            <el-table-column prop="waitTask" label=""></el-table-column>
            <el-table-column prop="overTask" label=""></el-table-column>
            <el-table-column prop="states" label=""></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click='popStop'></el-button>
                    <el-button type="text" size="small" @click='popUnit'></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-dialog
        title="sim"
        :visible.sync="saveInformation"
        width="30%">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
                <el-form-item label="SIM" prop="ascriptionSIM">
                    <el-select v-model="ruleForm.ascriptionSIM" placeholder="" @change='SIM_simChange'>
                        <el-option v-for='(i, index) in ascription_SIM' :value='i.mercSeq' :label="i.mercName" :key='"ascription_SIM" + index'></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="SIM" prop="SIMCard">
                    <el-input v-model="ruleForm.SIMCard"></el-input>
                </el-form-item>
                <el-form-item label="" prop="selectMachine">
                    <el-select v-model="ruleForm.selectMachine" placeholder="" @change='SIM_machineChange'>
                        <el-option v-for='(i, index) in machine_List' :value='i' :key='"machine_List" + index'></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="jackNum">
                    <el-select v-model="ruleForm.jackNum" placeholder="">
                        <el-option v-for='(i, index) in jackList' :value="i" :key='"jackList" + index'></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="saveInformation = false"> </el-button>
                <el-button type="primary" @click="submit('ruleForm')"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog
        :visible.sync="machineOccupy"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="machineOccupy = false"> </el-button>
                <el-button type="primary" @click='machineOccupy = false'> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog
        title=""
        :visible.sync="unite"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="unite = false"> </el-button>
                <el-button type="primary" @click='uniteSubmit'> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog
        title=""
        :visible.sync="uniteFail"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="uniteFail = false"> </el-button>
                <el-button type="primary" @click='uniteFail = false'> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog
        title=""
        :visible.sync="uniteSuccess"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="uniteSuccess = false"> </el-button>
                <el-button type="primary" @click='uniteSuccess = false'> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog
        title=""
        :visible.sync="stop"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="stop = false"> </el-button>
                <el-button type="primary" @click='stopSubmit'> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog
        title=""
        :visible.sync="stopFail"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="stopFail = false"> </el-button>
                <el-button type="primary" @click='stopSubmit'> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog
        title=""
        :visible.sync="stopSuccess"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="stopSuccess = false"> </el-button>
                <el-button type="primary" @click='stopSuccess = false'> </el-button>
            </span>
        </el-dialog>

        <el-pagination
            background
            layout="total, sizes, prev, pager, next, jumper"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync = "page.pagecurrent"
            :total='page.pagecount'
            v-if="page.pagecount != 0">
        </el-pagination>
    </el-main>
    <el-footer>
        <!-- <el-checkbox label=""></el-checkbox>
        <el-button type='info' style="margin-left: 20px;">
            
        </el-button> -->
    </el-footer>
</el-container>
</template>

<script>
import {baseUrl} from '../../assets/api/api'
export default {
    data: function() {
        return {
            tableData: [],
            saveInformation: false,  //
            machineOccupy: false,   //
            unite: false,   //
            uniteFail: false,   //
            uniteSuccess: false,    //
            stop: false,    //
            stopFail: false,
            stopSuccess: false, //
            ruleForm: {
                ascriptionSIM: '',   //sim
                SIMCard: '',    //SIM
                selectMachine: '',  //
                jackNum: '',    //
            },
            ascription_SIM: [],  //sim
            machine_List: [],    //
            jackList: [],       //
            cellList: {},    //
            page: {
                pagecurrent: 1,
                pagecount: 0
            },
            search: {
                name: "",
                code: "",
                number: ""
            },
            rules: {
                ascriptionSIM: [
                    { required: true, message: 'sim', trigger: 'change' }
                ],
                SIMCard: [
                    { required: true, message: 'sim', trigger: 'blur' },
                ],
                selectMachine: [
                    { required: true, message: '', trigger: 'change' },
                ],
                jackNum: [
                    { required: true, message: '', trigger: 'change' },
                ]
            }
        }
    },
    created: function() {
        var _this = this;
        // sim
        this.tableInit(1, 10);
        // sim
        this.$axios.get(baseUrl + '/merchant/getMerchantList').then(res => {
            _this.ascription_SIM = res.data;
        }).catch(err => {
            console.log(err);
        })
    },
    methods: {
        handleSizeChange(val) {
            this.tableInit(1, val);
        },
        handleCurrentChange(val) {
            this.tableInit(val, 10);
            console.log(this.page.pagecurrent);
        },
        /**
         * SIM
         * @return {[type]} [description]
         */
        SIM_simChange: function() {
            this.ruleForm.selectMachine = "";
            this.ruleForm.jackNum = "";
            var _this = this;
            // mercSeq
            var sendObj = {
                mercSeq: parseInt(this.ruleForm.ascriptionSIM)
            }
            $.ajax({
                url: baseUrl + '/sim/getDeviceByMercSeq',
                type: 'POST',
                data: sendObj,
                success: function(res) {
                    _this.machine_List = res;
                },
                error: function(err) {
                    console.log(err);
                }
            })
        },
        tableInit: function(page, size) {
            var _this = this;
            var params = new URLSearchParams();
            params.append('simNum', this.search.name);
            params.append('simMachineCode', this.search.code);
            params.append('simHubIndex', this.search.number);
            params.append('page', page);
            params.append('size', size);
            this.$axios.post(baseUrl + '/sim/getSimVOListByPage', params).then(res => {
                _this.tableData = res.data.dataInfo;
                this.page.pagecount = res.data.count;
                _this.tableData.forEach(function(ele, index) {
                    //
                    var simEntryTime = new Date(ele.simEntryTime);
                    ele.entry_time = simEntryTime.getFullYear() + '' + (simEntryTime.getMonth() + 1) + '' + simEntryTime.getDate() + '';
                    //
                    var agentAllowTime = new Date(ele.agentAllowTime);
                    ele.agentAllow_time = agentAllowTime.getFullYear() + '' + (agentAllowTime.getMonth() + 1) + '' + agentAllowTime.getDate() + '';
                    //
                    var mercAllowTime = new Date(ele.mercAllowTime);
                    ele.mercAllow_time = mercAllowTime.getFullYear() + '' + (mercAllowTime.getMonth() + 1) + '' + mercAllowTime.getDate() + '';

                    //
                    if (ele.simStatus == 0) {
                        ele.states = '';
                    } else if (ele.simStatus == 1) {
                        ele.states = '';
                    } else if (ele.simStatus == 2) {
                        ele.states = '';
                    }
                })
            }).catch(err => {
                console.log(err);
            });
        },
        SIM_machineChange: function() {
            var _this = this;
            var sendObj = {
                mercSeq: parseInt(this.ruleForm.ascriptionSIM),
                deviceCode: this.ruleForm.selectMachine
            }
            $.ajax({
                url: baseUrl + '/sim/getPortByDevice',
                type: 'POST',
                data: sendObj,
                success: function(res) {
                    _this.jackList = res;
                },
                error: function(err) {
                    console.log(err);
                }
            })
        },
        submit: function(formName) {
            var _this = this;
            var sendObj = {
                mercSeq: this.ruleForm.ascriptionSIM,
                simNum: this.ruleForm.SIMCard,
                simMachineCode: this.ruleForm.selectMachine,
                simHubIndex: this.ruleForm.jackNum
            }
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    $.ajax({
                        url: baseUrl + '/sim/addSimInfo',
                        type: 'POST',
                        data: sendObj,
                        success: function(res) {
                            console.log(res)
                            _this.saveInformation = false;
                            if (res == 1) {
                                _this.$message({
                                    message: '1:sim2:',
                                    type: 'warning'
                                });
                            } else if (res == 0) {
                                _this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                _this.tableInit(1, 10);
                                this.page.pagecurrent = 1;
                            }
                        },
                        error: function(err) {
                            console.log(err);
                        }
                    })
                } else {
                    _this.$message({
                        message: '',
                        type: 'error'
                    });
                    return false;
                }
            });
        },
        // 
        popUnit: function() {
            var _this = this;
            this.$nextTick(function() {
                $.ajax({
                    url: baseUrl + '/sim/deleteSimCheck',
                    type: 'POST',
                    data: {
                        simNum: _this.cellList.simNum
                    },
                    success: function(res) {
                        if (res == 0) {
                            _this.unite = true;
                            _this.tableInit(1, 10);
                            _this.page.pagecurrent = 1;
                        } else if (res == 1) {
                            _this.uniteFail = true;
                        }
                    },
                    error: function(err) {
                        console.log(err);
                    }
                })
            })
        },
        //
        uniteChange(row) {
            this.cellList = row;
        },
        // 
        uniteSubmit: function() {
            var _this = this;
            var sendObj = {
                simNum: this.cellList.simNum,
                simMachineCode: this.cellList.simMachineCode,
                simHubIndex: this.cellList.simHubIndex
            }
            $.ajax({
                url: baseUrl + '/sim/deleteSim',
                type: 'POST',
                data: sendObj,
                success: function(res) {

                    if (res == 0) {
                        _this.unite = false;
                        _this.uniteSuccess = true;
                        _this.tableInit(1, 10);
                        _this.page.pagecurrent = 1;
                    }
                },
                error: function(err) {
                    console.log(err);
                }
            })
        },

        //
        popStop: function() {
            var _this = this;
            this.$nextTick(function() {
                $.ajax({
                    url: baseUrl + '/sim/deleteSimCheck',
                    type: 'POST',
                    data: {
                        simNum: _this.cellList.simNum
                    },
                    success: function(res) {
                        if (res == 0) {
                            _this.stop = true;
                        } else if (res == 1) {
                            _this.stopFail = true;
                        }
                    },
                    error: function(err) {
                        console.log(err);
                    }
                })
            })
        },

        //
        stopSubmit: function() {
            var _this = this;
            var sendObj = {
                simNum: this.cellList.simNum,
                simMachineCode: this.cellList.simMachineCode,
                simHubIndex: this.cellList.simHubIndex
            }
            $.ajax({
                url: baseUrl + '/sim/stopSim',
                type: 'POST',
                data: sendObj,
                success: function(res) {
                    if (res == 0) {
                        _this.stop= false;
                        _this.stopFail = false;
                        _this.stopSuccess = true;
                        _this.tableInit(1, 10);
                        _this.page.pagecurrent = 1;
                    }
                },
                error: function(err) {
                    console.log(err);
                }
            })
        }
    }
}
</script>

<style lang="less">
html, body, #app, .el-container {
    height: 100%;
}
.simCard {
    .el-header {
        line-height: 60px;
        font-size: 22px;
        color: #3399ff;
        background-color: #fff;
        position: relative;
        .el-button {
            &:nth-of-type(1) {
                position: absolute;
                right: 20px;
                top: 10px;
            }
            &:nth-of-type(2) {
                position: absolute;
                right: 140px;
                top: 10px;
            }
        }
    }
    .el-main {
        .search {
            padding: 14px 0;
            background-color: #f0f3f6;
            .center {
                background-color: #fff;
                line-height: 88px;
                padding: 0 20px;
                color: #333;
                font-size: 16px;
                letter-spacing: 3px;
                font-weight: bold;
                .el-input {
                    margin-right: 20px;
                    width: auto;
                    input {
                        background-color: #f0f3f6;
                    }
                }
            }
        }
    }
    .el-form-item .el-select {
        width: 100%;
    }
    .cell {
        text-align: center;
    }
    .el-footer {
        line-height: 60px;
    }
}
</style>



