<template lang="html">
<!--  -->
<el-container class="merchantExamine">
    <el-header>
        
    </el-header>
    <el-main>
        <!--  -->
        <div class="search">
            <div class="center">
                //:
                <el-input v-model="search.name"></el-input>
                :
                <el-input v-model="search.person"></el-input>
                :
                <el-date-picker
                    v-model="search.beginTime"
                    type="date"
                    placeholder=""
                    value-format="yyyy-MM-dd">
                </el-date-picker>
                
                <el-date-picker
                    v-model="search.endTime"
                    type="date"
                    placeholder=""
                    value-format="yyyy-MM-dd">
                </el-date-picker>
                <el-button type='danger' @click="tableInit(1, 10)"></el-button>
            </div>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data='tableData.slice((this.pageNum-1)*this.pageSize, this.pageNum*this.pageSize)' tooltip-effect="dark" style="width: 100%" stripe
        @cell-click='changeExamine'>
            <el-table-column prop='MERC_SEQ' label=""></el-table-column>
            <el-table-column prop="MERC_NAME" label=""></el-table-column>
            <el-table-column prop="MERC_BEGIN_TIME" label=""></el-table-column>
            <el-table-column prop="AGENT_NAME" label=""></el-table-column>
            <el-table-column prop="MERC_CHECK_STATUS" label=""></el-table-column>
            <el-table-column prop="CHECK_REMARK" label=""></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="lookInformation"></el-button>
                    <el-button type="text" size="small" @click="examine = true"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-dialog
        title=''
        :visible.sync="examine"
        width="20%">
            : <el-button type="primary" plain :query='0' @click='checkStatusSuccess'></el-button>
                <el-button type="primary" plain :query='2' @click='checkStatusFail'></el-button>
                <div>
                    :<el-input
                        type="textarea"
                        :rows="2"
                        placeholder=""
                        v-model="textarea"
                        resize='none'>
                    </el-input>
                </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="examine = false"> </el-button>
                <el-button type="primary" @click='examineSubmit'> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog
        title=''
        :visible.sync="examineSuc"
        width="20%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="examineSuc = false"> </el-button>
                <el-button type="primary" @click='examineSuc = false'> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog
        title=''
        :visible.sync="bussinessInformation"
        width="30%" class="infor">
            <div>: {{ information.mercName }}</div>
            <div>: {{ information.area }}</div>
            <div>: {{ information.industryName }}</div>
            <div>/: {{ information.mercPwd }}</div>
            <div>: {{ information.mercBusinessEntityName }}</div>
            <div>: {{ information.mercBusinessEntityContactName }}</div>
            <div>: {{ information.mercBusinessEntityContactPhone }}</div>
            <div>: {{ information.mercBusinessMail }}</div>
            <div>
                
            </div>
            <div>
                <img src="/static/gray-1.png" alt="">
            </div>
        </el-dialog>
        <!--  -->
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            background
            :current-page.sync="page.pagecurrent"
            layout="total, prev, pager, next, sizes, jumper"
            :total="page.pagecount">
        </el-pagination>
    </el-main>
</el-container>
</template>

<script>
import {baseUrl} from '../../assets/api/api'
export default {
    data: function() {
        return {
            tableData: [],          //
            pageSize: 10,       //
            pageNum: 1,          //
            examine: false,      //
            examineSuc: false,
            bussinessInformation: false,
            textarea: '',
            row: {},
            mercCheckStatus: 0,     //
            information: {},         //
            page: {
                pagecount: 0,
                pagecurrent: 1
            },
            search: {
                name: '',
                person: '',
                beginTime: '',
                endTime: ''
            }
        }
    },
    created: function() {
        var _this = this;
        this.tableInit(1, 10);
    },
    methods: {
        handleSizeChange(val) {
            this.tableInit(1, val);
        },
        handleCurrentChange(val) {
            this.tableInit(val, 10);
        },
        /**
         * 
         * @param  {[type]} page [description]
         * @param  {[type]} size [description]
         * @return {[type]}      [description]
         */
        tableInit: function(page, size) {
            var _this = this;
            var params = new URLSearchParams();
            params.append('mercName', this.search.name);
            params.append('agentName', this.search.person);
            params.append('startTime', this.search.beginTime);
            params.append('endTime', this.search.endTime);
            params.append('page', page);
            params.append('size', size);
            this.$axios.post(baseUrl + '/merchantcheck/getMerchantCheckList', params).then(res => {
                res.data.dataInfo.forEach(function(ele, index) {
                    var now = new Date(ele.MERC_BEGIN_TIME);
                    ele.MERC_BEGIN_TIME = now.getFullYear() + '' + (now.getMonth() + 1) + '' + now.getDate() + '';
                    if (ele.MERC_CHECK_STATUS == 1) {
                        ele.MERC_CHECK_STATUS = '';
                    } else if (ele.MERC_CHECK_STATUS == 2) {
                        ele.MERC_CHECK_STATUS = '';
                    }
                })
                _this.tableData = res.data.dataInfo;
                _this.page.pagecount = res.data.count;
            }).catch(err => {
                console.log(err);
            })
        },
        changeExamine(row) {
            this.row = row;
        },
        checkStatusSuccess: function() {
            this.mercCheckStatus = 0;
        },
        checkStatusFail: function() {
            this.mercCheckStatus = 2;
        },
        examineSubmit: function() {
            var _this = this;
            var sendObj = {
                mercSeq: this.row.MERC_SEQ,
                mercCheckStatus: this.mercCheckStatus,
                checkRemark: this.textarea
            };
            $.ajax({
                url: baseUrl + '/merchantcheck/checkMerchant',
                type: 'post',
                data: sendObj,
                success: function(res) {
                    _this.examine = false;
                    _this.examineSuc = true;
                    _this.tableInit(1, 10);
                    _this.page.pagecurrent = 1;
                },
                error: function(err) {
                    console.log(err);
                }
            })
        },
        lookInformation: function() {
            var _this = this;
            var params = new URLSearchParams();
            setTimeout(function() {
                params.append('mercSeq', _this.row.MERC_SEQ);
                _this.$axios.post(baseUrl + '/merchantcheck/getMerchantByMercSeq', params).then(res => {
                    _this.information = res.data;
                    console.log(res);
                    _this.information.area = _this.information.provinceName + _this.information.cityName;
                    console.log(_this.information);
                }).catch(err => {
                    console.log(err);
                })
            }, 500)
            this.$nextTick(function() {
                _this.bussinessInformation = true;
            })
        }
    }
}
</script>

<style lang="less">
html, body, #app, .el-container {
    height: 100%;
}
.content {
    overflow: auto;
}
.merchantExamine {
    .infor {
        .el-dialog__body {
            div {
                text-align: center;
                margin-top: 20px;
            }
        }
    }
    .el-header {
        line-height: 60px;
        font-size: 22px;
        color: #3399ff;
        background-color: #fff;
    }
    .el-main {
        .search {
            padding: 14px 0;
            background-color: #f0f3f6;
            .center {
                background-color: #fff;
                line-height: 88px;
                padding: 0 20px;
                color: #333;
                font-size: 16px;
                letter-spacing: 3px;
                font-weight: bold;
                .el-input {
                    margin-right: 20px;
                    width: auto;
                    input {
                        background-color: #f0f3f6;
                    }
                    &:not(:nth-of-type(1)) {
                        input {
                            width: 120px;
                        }
                    }
                }
            }
        }
        .cell {
            text-align: center;
        }
    }
}
</style>



