<template lang="html">
<!--  -->
<el-container class="jackDetail">
    <el-header>
        <i onclick="javascript:history.back(-1)" @click='goBack'></i>
        
    </el-header>
    <el-main>
        <!--  -->
        <div class="search">
            <div class="center">
                //:
                <el-input v-model="search.name"></el-input>
                :
                <el-input v-model="search.card"></el-input>
                :
                <el-select v-model="search.status">
                    <el-option v-for="(i, index) in search.statusList" :label="i.value" :value="i.id" :key="String(index)"></el-option>
                </el-select>
                <el-button type='danger' @click="tableInit(1, 10)"></el-button>
            </div>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :data='tableData' tooltip-effect="dark" style="width: 100%" stripe @cell-click='relationChange'>
            <el-table-column prop='deviceCode' label=""></el-table-column>
            <el-table-column prop="slotIndex" label=""></el-table-column>
            <el-table-column prop="agentName" label=""></el-table-column>
            <el-table-column prop="simNum" label="SIM"></el-table-column>
            <el-table-column prop="entryTime" label=""></el-table-column>
            <el-table-column prop="mercName" label=""></el-table-column>
            <el-table-column prop="remain_time" label=""></el-table-column>
            <el-table-column prop="allow_time" label=""></el-table-column>
            <el-table-column prop="states" label=""></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click='reviseStates = true'></el-button>
                    <el-button type="text" size="small" @click='relatioBroken = true'></el-button>
                </template>
            </el-table-column>
        </el-table>


        <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="page.currentPage1"
            :page-size="10"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.pagecount"
            v-if="page.pagecount != 0">
        </el-pagination>
        <!--  -->
        <el-dialog
        title=""
        :visible.sync="relatioBroken"
        width="30%">
            
            <el-select v-model='chooseRelation' @change='selectMerchant'>
                <el-option value=""></el-option>
                <el-option value=""></el-option>
            </el-select>
            <span slot="footer" class="dialog-footer">
                <el-button @click="relatioBroken = false"> </el-button>
                <el-button type="primary" @click='selectMerchantSubmit'> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
        title=""
        :visible.sync="reviseStates"
        width="30%">
            
            <el-select v-model='chooseReviseStates' @change='reviseStatesChange'>
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
            </el-select>
            <span slot="footer" class="dialog-footer">
                <el-button @click="reviseStates = false"> </el-button>
                <el-button type="primary" @click='reviseStatesSubmit'> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
        title=""
        :visible.sync="successDialog"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="successDialog = false"> </el-button>
                <el-button type="primary" @click='successDialog = false'> </el-button>
            </span>
        </el-dialog>
    </el-main>
</el-container>
</template>

<script>
import {baseUrl} from '../../assets/api/api'
export default {
    data: function() {
        return {
            tableData: [],
            relatioBroken: false,    //
            reviseStates: false,    //
            successDialog: false,
            chooseRelation: '',     //
            chooseReviseStates: "",
            slotStates: '',      //
            DEVICE_CODE: '',    ///code
            SLOT_INDEX: '',     ///
            SIM_NUM: '',          ///
            SELECTMerchantName: '',     //
            reviseStatesName: '',    //
            reviseStatesInt: 0,
            page: {
                pagecount: 0,    //table
                currentPage1: 1,
                size: 0
            },
            search: {
                name: "",
                card: "",
                status: "",
                statusList: [
                    {
                        value: "",
                        id: 0
                    },
                    {
                        value: "",
                        id: 1
                    },
                    {
                        value: "",
                        id: 2
                    }
                ]
            }
        }
    },
    created: function() {
        var _this = this;
        var jackDetailTableData = JSON.parse(sessionStorage.getItem('jackDetailValue'));
        var pagecount = sessionStorage.getItem('count');
        this.page.pagecount = parseInt(pagecount);
        this.tableData = jackDetailTableData;
        this.tableData.forEach(function(ele, index) {
            //
            var SIM_ENTRY_TIME = new Date(ele.simEntryTime);
            //
            if (SIM_ENTRY_TIME != 'Invalid Date') {
                ele.entryTime = SIM_ENTRY_TIME.getFullYear() + '.' + (SIM_ENTRY_TIME.getMonth() + 1) + '.' + SIM_ENTRY_TIME.getDate();
            }

            // 
            var REMAIN_TIME = ele.allowTime - ele.startTime;
            if (REMAIN_TIME != 'Invalid Date') {
                ele.remain_time = parseInt(REMAIN_TIME/1000/3600/24) + '';
                if (ele.remain_time == 'NaN') {
                    ele.remain_time = '';
                }
            }

            //
            var ALLOW_TIME = new Date(ele.allowTime);
            if (ALLOW_TIME != 'Invalid Date') {
                ele.allow_time = ALLOW_TIME.getFullYear() + '.' + (ALLOW_TIME.getMonth() + 1) + '.' + ALLOW_TIME.getDate();
            }
            //
            if (ele.slotStatus == 0) {
                ele.states = '';
            } else if (ele.slotStatus == 1) {
                ele.states = '';
            } else if (ele.slotStatus == 2) {
                ele.states = '';
            }
        })
    },
    methods: {
        handleSizeChange(val) {
            this.size = val;
            this.tableInit(1, val);
        },
        handleCurrentChange(val) {
            var that = this;
            this.tableInit(val, 10);
        },
        tableInit: function(page, size) {
            var that = this;
            var deviceCode = sessionStorage.getItem('deviceCode');
            $.ajax({
                url: baseUrl + '/deviceport/getDevicePortListByPage',
                type: 'POST',
                data: {
                    search: that.search.name,
                    slotIndex: that.search.card,
                    slotStatus: that.search.status,
                    deviceCode: deviceCode,
                    page: page,
                    size: size
                },
                success: function(res) {
                    that.tableData = res.dataInfo;
                    that.tableData.forEach(function(ele, index) {
                        //
                        var SIM_ENTRY_TIME = new Date(ele.simEntryTime);
                        //
                        if (SIM_ENTRY_TIME != 'Invalid Date') {
                            ele.entryTime = SIM_ENTRY_TIME.getFullYear() + '.' + (SIM_ENTRY_TIME.getMonth() + 1) + '.' + SIM_ENTRY_TIME.getDate();
                        }

                        // 
                        var REMAIN_TIME = ele.allowTime - ele.startTime;
                        if (REMAIN_TIME != 'Invalid Date') {
                            ele.remain_time = parseInt(REMAIN_TIME/1000/3600/24) + '';
                            if (ele.remain_time == 'NaN') {
                                ele.remain_time = '';
                            }
                        }

                        //
                        var ALLOW_TIME = new Date(ele.allowTime);
                        if (ALLOW_TIME != 'Invalid Date') {
                            ele.allow_time = ALLOW_TIME.getFullYear() + '.' + (ALLOW_TIME.getMonth() + 1) + '.' + ALLOW_TIME.getDate();
                        }

                        //
                        if (ele.slotStatus == 0) {
                            ele.states = '';
                        } else if (ele.slotStatus == 1) {
                            ele.states = '';
                        } else if (ele.slotStatus == 2) {
                            ele.states = '';
                        }
                    })
                },
                error: function(err) {
                    console.log(err);
                }
            })
        },
        goBack: function() {
            //
            sessionStorage.removeItem('jackDetailValue');
        },
        selectMerchant(name) {
            this.SELECTMerchantName = name;
        },
        reviseStatesChange(name) {
            this.reviseStatesName = name;
        },
        relationChange(row) {
            this.DEVICE_CODE = row.deviceCode;
            this.SLOT_INDEX = row.slotIndex;
            this.SIM_NUM = row.simNum;
        },
        selectMerchantSubmit: function() {
            var that = this;
            var submitObj = {
                deviceCode: this.DEVICE_CODE,
                slotIndex: this.SLOT_INDEX,
                simNum: this.SIM_NUM
            }
            if (this.SELECTMerchantName == '') {
                $.ajax({
                    url: baseUrl + '/deviceport/removeMerchant',
                    type: 'POST',
                    async: false,
                    data: submitObj,
                    success: function(res) {
                        that.$message({
                            message: '',
                            type: 'success'
                        });
                        that.tableInit(1, 10);
                        that.page.currentPage1 = 1;
                    },
                    error: function(err) {
                        console.log(err);
                    }
                })
            } else if (this.SELECTMerchantName == '') {
                $.ajax({
                    url: baseUrl + '/deviceport/removeAgent',
                    type: 'POST',
                    async: false,
                    data: submitObj,
                    success: function(res) {
                        that.$message({
                            message: '',
                            type: 'success'
                        });
                        that.tableInit(1, 10);
                        that.page.currentPage1 = 1;
                    },
                    error: function(err) {
                        console.log(err);
                    }
                })
            }
            this.relatioBroken = false;
        },
        reviseStatesSubmit: function() {
            var that = this;
            var submitObj = {
                deviceCode: this.DEVICE_CODE,
                slotIndex: this.SLOT_INDEX,
                slotStatus: this.chooseReviseStates
            }
            console.log(submitObj);
            $.ajax({
                url: baseUrl + '/deviceport/updateSlotStatus',
                type: 'POST',
                data: submitObj,
                async: false,
                success: function(res) {
                    if (res == 0) {
                        that.$message({
                            message: '',
                            type: 'success'
                        });
                        that.tableInit(1, 10);
                        that.page.currentPage1 = 1;
                    }
                },
                error: function(err) {
                    console.log(err);
                }
            })
            this.reviseStates = false;
            this.successDialog = true;
        }
    }
}
</script>

<style lang="less">
html, body, #app, .el-container {
    height: 100%;
}
.jackDetail {
    .el-header {
        line-height: 60px;
        font-size: 22px;
        color: #3399ff;
        background-color: #fff;
        position: relative;
        i {
            display: inline-block;
            width: 22px;
            height: 16px;
            background: url(/static/back.png) no-repeat center/22px 16px;
            margin-right: 10px;
        }
    }
    .el-main {
        .search {
            padding: 14px 0;
            background-color: #f0f3f6;
            .center {
                background-color: #fff;
                line-height: 88px;
                padding: 0 20px;
                color: #333;
                font-size: 16px;
                letter-spacing: 3px;
                font-weight: bold;
                .el-input {
                    margin-right: 20px;
                    width: auto;
                    input {
                        background-color: #f0f3f6;
                    }
                    &:not(:nth-of-type(1)) {
                        input {
                            width: 120px;
                        }
                    }
                }
                .el-select {
                    width: 120px;
                }
            }
        }
        .cell {
            text-align: center;
        }
    }
}
</style>



