<template lang="html">
    <div class="userPower">
        <el-header class="content-header agentPowerHeader">
            <el-menu :default-active="'2'" class="el-menu-demo" mode="horizontal">
                <el-menu-item index="1" @click='agentPower'></el-menu-item>
                <el-menu-item index="2"></el-menu-item>
            </el-menu>
            <el-button type="primary" @click="dialogVisible = true"></el-button>
            <el-button type="primary" @click='jump'></el-button>
        </el-header>
        <!-- input -->
        <div class="content-ipt agentPowerContent">
            <div class="center">
                /
                <el-input v-model="search.name" clearable></el-input>
                
                <el-select v-model='agentRoleSearchVal'>
                    <el-option v-for='(i, index) in dialogAgentRole' :label='i.groupName' :value='i.groupName' :key='"agentPowerRole1" + index'></el-option>
                </el-select>
                <el-button type="danger" @click="tableInit"></el-button>
            </div>
        </div>
        <!--  -->
        <el-table
        ref="multipleTable"
        :data='tableData'
        tooltip-effect="dark"
        style="width: 100%"
        :row-class-name="tableRowClassName"
        @cell-click='getRow'>
            <el-table-column type="selection"></el-table-column>
            <el-table-column prop='userSeq' label=""></el-table-column>
            <el-table-column prop="userName" label=""></el-table-column>
            <el-table-column prop="groupName" label=""></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click='addAccountNum();dialog.modify.status = true'></el-button>
                    <el-button type="text" size="small" @click="dialog.rewritePw = true"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-dialog class="agentPowerDialog"
        title=""
        :visible.sync="dialogVisible"
        width="30%">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
            <el-form-item label="" prop="agentNameVal">
                <el-input v-model='ruleForm.agentNameVal' clearable></el-input>
            </el-form-item>
            <el-form-item label="" prop="accountNum">
                <el-input v-model='ruleForm.accountNum' clearable></el-input>
            </el-form-item>
            <el-form-item label="" prop="userPw">
                <el-input v-model='ruleForm.userPw' clearable></el-input>
            </el-form-item>
            <el-form-item label="" prop="agentRoleVal">
                <el-select v-model='ruleForm.agentRoleVal' @change='changeRole'>
                    <el-option v-for='(i, index) in dialogAgentRole' :label='i.groupName' :value='i.groupId' :key='"agentPowerDialog2" + index'></el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="checkAccount('ruleForm')"> </el-button>
        </span>
        </el-dialog>

        <!--  -->
        <el-dialog class="agentPowerDialog"
        title=""
        width='40%'
        :visible.sync="dialog.modify.status">
        <div class="name">
            
            <el-input v-model='dialog.modify.accountNum' disabled></el-input>
        </div>
        <div class="name">
            
            <el-input v-model='dialog.modify.name' clearable></el-input>
        </div>
        <div class="name">
            
            <el-select v-model='dialog.modify.role'>
                <el-option v-for='(i, index) in dialogAgentRole' :label='i.groupName' :value='i.groupId' :key='"agentPowerDialog2" + index'></el-option>
            </el-select>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialog.modify.status = false"> </el-button>
            <el-button type="primary" @click="modifyClick"> </el-button>
        </span>
        </el-dialog>

        <el-dialog
        width='30%'
        :visible.sync="dialogShow">
            <span></span>
            <div slot='footer'>
                <el-button type="primary" @click="dialogShow = false"> </el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog
        width='30%'
        :visible.sync="dialog.rewritePw">
            <span>?</span>
            <div slot='footer'>
                <el-button type="primary" @click="rewritePassWord();dialog.rewritePw = false"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import {baseUrl} from '../../assets/api/api'
export default {
    data() {
        return {
            tableData: [],
            dialogVisible: false,
            dialogShow: false,
            dialogShowValue: '',
            dialogAgentName: [],
            dialogAgentNameVal: '',
            dialogAgentRole: [],
            dialogAgentRoleVal: '',
            agentRoleSearchVal: '',
            roleName: '',
            nameName: '',
            dialogAccountNum: '',
            row: {},
            search: {
                name: ""
            },
            dialog: {
                modify: {
                    status: false,
                    role: '',
                    name: '',
                    accountNum: ''
                },
                addAgent: {
                    agentNameVal: '',
                    accountNum: '',
                    agentRoleVal: '',
                    userPw: ''
                },
                rewritePw: false
            },
            ruleForm: {
                agentNameVal: '',
                accountNum: '',
                agentRoleVal: '',
                userPw: ''
            },
            rules: {
                agentNameVal: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                accountNum: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                agentRoleVal: [
                    { required: true, message: '', trigger: 'change' },
                ],
                userPw: [
                    { required: true, message: '', trigger: 'blur' },
                ]
            }
        }
    },
    created: function() {
        var _this = this;
        this.tableInit();

        //
        this.$axios.get(baseUrl + '/usergroup/getUserGroupList').then(res => {
            _this.dialogAgentRole = res.data;
        }).catch(err => {
            console.log(err);
        })
    },
    methods: {
        changeRole(name) {
            this.roleName = name;
        },
        changeName(name) {
            this.nameName = name;
            this.dialogAgentName.map(function(currentValue) {
                if (currentValue.agentSeq == 3) {
                    console.log(currentValue.agentBusinessEntityContactPhone);
                }
            })
        },
        getRow(row) {
            this.row = row;
            console.log(row);
        },
        /**
         * 
         * @return {[type]} [description]
         */
        tableInit: function() {
            var _this = this;
            var params = new URLSearchParams();
            params.append("userName", this.search.name);
            params.append("groupName", this.agentRoleSearchVal);
            this.$axios.post(baseUrl + '/usergroup/getUserGroupVOList', params).then(res => {
                _this.tableData = res.data;
            }).catch(err => {
                console.log(err);
            })
        },
        /**
         * 
         * @return {[type]} [description]
         */
        rewritePassWord: function() {
            var that = this;
            $.ajax({
                url: baseUrl + '/user/resetPwd',
                type: 'post',
                data: {
                    userSeq: that.row.userSeq
                },
                success: function(res) {
                    that.dialogShow = true;
                },
                error: function(err) {
                    console.log(err);
                }
            })
        },
        /**
         * 
         * @return {[type]} [description]
         */
        addAccountNum: function() {
            var that = this;
            this.$nextTick(function() {
                $.ajax({
                    url: baseUrl + '/user/getUserByUserSeq',
                    type: 'post',
                    data: {
                        userSeq: that.row.userSeq
                    },
                    success: function(res) {
                        that.dialog.modify.accountNum = res.userName;
                        that.dialog.modify.name = res.realName;
                    },
                    error: function(err) {
                        console.log(err);
                    }
                })
            })
        },
        /**
         * 
         * @return {[type]} [description]
         */
        modifyClick: function() {
            var that = this;

            var sendObj = {
                userSeq: this.row.userSeq,
                realName: this.dialog.modify.name,
                groupId: this.dialog.modify.role
            }
            $.ajax({
                url: baseUrl + '/user/editUser',
                type: 'post',
                data: sendObj,
                success: function(res) {
                    if (res == 0) {
                        that.dialog.modify.status = false;
                        that.dialogShow = true;
                    }
                },
                error: function(err) {
                    console.log(err);
                }
            })
        },
        /**
         * 
         * @return {[type]} [description]
         */
        checkAccount: function(formName) {
            var _this = this;

            _this.dialogVisible = false;

            var params = new URLSearchParams();
            params.append('realName', _this.ruleForm.agentNameVal);
            params.append('userName', _this.ruleForm.accountNum);
            params.append('userPwd', _this.$md5(_this.ruleForm.userPw));
            params.append('groupId', _this.ruleForm.agentRoleVal);

            this.$refs[formName].validate((valid) => {
                if (valid) {
                    _this.$axios.post(baseUrl + '/user/addUser', params).then(res => {
                        _this.$message({
                            message: '',
                            type: 'success'
                        });
                        //
                        _this.$axios.get(baseUrl + '/usergroup/getUserGroupVOList').then(res => {
                            _this.tableData = res.data;
                        }).catch(err => {
                            console.log(err);
                        })
                    }).catch(err => {
                        console.log(err);
                    })
                } else {
                    _this.$message({
                        message: '',
                        type: 'error'
                    });
                    return false;
                }
            })
        },
        jump: function() {
            this.$router.push('/permissionConfig');
        },
        agentPower: function() {
            this.$router.push('/agentPower')
        },
        tableRowClassName({row, rowIndex}) {
            if (rowIndex === 1) {
                return 'warning-row';
            } else if (rowIndex === 3) {
                return 'success-row';
            }
            return '';
        }
    }
}
</script>

<style lang="less">
.userPower {
    .el-table .warning-row {
        background: oldlace;
    }
    .el-table .success-row {
        background: #f0f9eb;
    }
    .agentPowerHeader {
        line-height: 60px;
        position: relative;
        background-color: #fff;
        color: #3399ff;
        font-size: 22px;
        & .el-button:nth-of-type(1) {
            position: absolute;
            right: 145px;
            top: 10px;
            width: 100px;
        }
        & .el-button:nth-of-type(2) {
            width: 100px;
            position: absolute;
            right: 20px;
            top: 10px;
        }
    }
    .agentPowerContent {
        background-color: #f0f3f6;
        padding: 14px 0;
        .center{
            position: relative;
            background-color: #fff;
            line-height: 88px;
            padding: 0 20px;
            .el-input {
                width: auto;
            }
            .el-button {
                position: absolute;
                right: 20px;
                top: 25px;
                width: 100px;
            }
        }
    }
    .agentPowerDialog {
        .name {
            width: 100%;
            text-align: center;
            color: #333;
            font-size: 16px;
        }
        & .name:not(:nth-of-type(1)) {
            margin-top: 20px;
        }
    }
    .cell {
        text-align: center;
    }
}
</style>



